body, h2 {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

a {
  text-decoration: none;
}

.page {
  font-family: Roboto, Arial, Helvetica, sans-serif;
}

.page__header {
  padding-left: 50px;
  padding-right: 50px;
}

.card {
  box-sizing: border-box;
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  align-items: center;
  width: 200px;
  transition: transform .3s, color .3s;
  display: flex;
}

.card__img {
  width: 160px;
  height: 134px;
  margin-block: 32px 40px;
}

.card__info {
  padding-inline: 16px;
  padding-bottom: 16px;
}

.card__title {
  color: #060b35;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  text-decoration: none;
  display: block;
}

.card__product-code {
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  line-height: 14px;
  display: block;
}

.card__rating {
  justify-content: space-between;
  margin-bottom: 24px;
  display: flex;
}

.card__reviews {
  color: #060b35;
  align-self: center;
  font-size: 10px;
  text-decoration: none;
}

.card__price-field {
  color: #616070;
  justify-content: space-between;
  margin-bottom: 16px;
  font-size: 12px;
  line-height: 18px;
  display: flex;
}

.card__price {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button-buy {
  box-sizing: border-box;
  color: #fff;
  text-transform: uppercase;
  text-align: center;
  background-color: #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition: color .3s, background-color .3s;
  display: flex;
}

.card__button-buy:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__title {
  color: #34568b;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star:not(:first-child) {
  margin-left: 4px;
}

.stars--1 :nth-child(-n+1), .stars--2 :nth-child(-n+2), .stars--3 :nth-child(-n+3), .stars--4 :nth-child(-n+4), .stars--5 :nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.catalog__list {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .catalog__list {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .catalog__list {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .catalog__list {
    grid-template-columns: repeat(4, 200px);
  }
}

.header {
  box-sizing: border-box;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__moyo-logo-link {
  line-height: 0;
}

.header__moyo-logo {
  height: 40px;
}

.nav {
  align-items: center;
  display: flex;
}

.nav__list {
  display: flex;
}

.nav__link {
  text-align: center;
  text-wrap: nowrap;
  text-transform: uppercase;
  color: #060b35;
  align-items: center;
  height: 60px;
  margin-left: 20px;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  transition: color .3s;
  display: flex;
}

.nav__link--is-active {
  color: #00acdc;
  margin-left: 0;
  position: relative;
}

.nav__link--is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 2px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.nav__link:hover {
  color: #00acdc;
}

